import styled from "styled-components";
import { useProgress } from "../../../contexts/ProgressContext";
import { useSizeRatio } from "../../../hooks/useSizeRatio";
import { Block } from "../Block";
import { Modal } from "./Modal";
import { Button } from "../Button";
import { WhiteStarPart } from "./WhiteStarPart";
import { RedStarPart } from "./RedStarPart";
import { useEffect, useState } from "react";

const Content = styled(Block)`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: var(--spacing_x5);
`;

const ButtonsWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    margin: var(--spacing_x4) 0 var(--spacing_x5);
    align-items: center;

    & button {
        width: calc((100% - var(--spacing_x3) + (var(--spacing_x1) / 2))/2);
    }
`;

const ButtonStyled = styled(Button)`
    margin: var(--spacing_x4) 0 var(--spacing_x5);
`;

const ProgressWrapper = styled.div`
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
`;

const ProgressCircle  = styled.div`
    width: ${({$ratio}) => $ratio * 6}px;
    height: ${({$ratio}) => $ratio * 6}px;
    border-radius: 50%;
    background-color: var(--color-${({$isActive}) => $isActive ? 'red' : 'black'});
    transition: background-color 0.2s;

    & + & {
        margin-left:  ${({$ratio}) => $ratio * 2.5}px;
    }
`;

export const InfoModal = () => {
    const ratio = useSizeRatio();
    const [part, setPart] = useState(0);
    const [checkTg, setCheckTg] = useState(false);
    const { user, setVipPoints, setModal, setUserInfo, updateUser, getUserInfo } = useProgress();
    const amount = user?.isVip ? 4 : 3;
    const progress = Array.from({length: amount}, (v, i) => i);

    const Progress = () => (
        <ProgressWrapper>
            {progress.map((p) => (
                <ProgressCircle key={p} $isActive={p === part} $ratio={ratio}/>
            ))}
        </ProgressWrapper>
    );

    const handleGoLobby = () => {
        updateUser({seenRules: true});
        setUserInfo({seenRules: true});
        setModal({visible: true, type: 'tg'});
    }
    const LastPart = (
        <Content>
            <p>
                  -:    .
            </p>
            <ButtonsWrapper>
                <Button color="pink" onClick={() => setPart(prev => prev - 1)}></Button>
                <Button color="red" onClick={handleGoLobby}> </Button>
            </ButtonsWrapper>
            <Progress />
        </Content>
    );

    const handleSetRedStarPart = () => {
        if (user.isVip && !user.weekStars.includes(1)) {
            setVipPoints(prev => prev + 1);
            setUserInfo({weekStars: [...user.weekStars, 1]})
        }
        setPart(prev => prev + 1)
    }

    useEffect(() => {
        const handleCheck = () => {
            if (user.isTgConnected || checkTg) return;

            setCheckTg(true);
            
            getUserInfo(user.email, true).finally(() => {
                setCheckTg(false);
            });
        }

        window.addEventListener('focus', handleCheck);

        return () => window.removeEventListener('focus', handleCheck);
    }, []);
    
    const getContent = () => {
        switch (part) {
            case 0:
                return (
                    <Content>
                        <p>
                              .  ,    {' '}
                                .   {' '}
                                  . 
                            {'\n\n'}
                            <b>    -!</b>
                        </p>
                        <ButtonStyled color="red" onClick={() => setPart(prev => prev + 1)}></ButtonStyled>
                        <Progress />
                    </Content>
                )
            case 1: 
                return (
                    <WhiteStarPart>
                        {
                            user?.isVip ? (
                                <p>
                                         {' '}
                                      .     <b>15</b> {' '}
                                    <b> </b>.
                                    {'\n\n'}
                                          <a href="https://t.me/Alfajourney_bot" target={"_blank"}>tg-</a>. 
                                    {'\n\n'}
                                          -  {' '}
                                      .  .
                                </p>
                            ) : (
                                <p>
                                           {' '}
                                    .{'\n'}  <b>60</b>  .{' '}
                                           ,  {' '}
                                     ID  <a href="https://t.me/Alfajourney_bot" target={"_blank"}><b>tg-</b></a>!{'\n\n'}
                                         -   <b>10</b>.{' '}
                                      .
                                </p>
                            )
                        }
                        <ButtonsWrapper>
                            <Button color="pink" onClick={() => setPart(prev => prev - 1)}></Button>
                            <Button color="red" onClick={handleSetRedStarPart}></Button>
                        </ButtonsWrapper>
                        <Progress />
                    </WhiteStarPart>
                );
            case 2: 
                if (user?.isVip) return (
                    <RedStarPart>
                        <ButtonsWrapper>
                            <Button color="pink" onClick={() => setPart(prev => prev - 1)}></Button>
                            <Button color="red" onClick={() => setPart(prev => prev + 1)}></Button>
                        </ButtonsWrapper>
                        <Progress />
                    </RedStarPart>
                ) 

                return LastPart;
            case 3: 
                return LastPart;
            default: break;
        }
    }

    return (
        <Modal isDarken isDisabledAnimation>
            {getContent()}
        </Modal>
    )
}